.aside{
    position: fixed;
    left: 0;
    top: 0;
    background: var(--body-color);
    border-right: 1px solid rgba(0, 0, 0, 0.05);
    padding: 2.5rem;
    width: 110px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 10;
}

.nav__list{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    list-style: none;
}
.nav__link{
    font-size: 1.5rem;
    color: var(--title-color);
    font-weight: var(--font-bold);
    transform: .3s;
}
.nav__link:hover{
    color: hsl(290, 100%, 68%);
}
.copyright{
    color: hsl(245, 15%, 65%);
    font-size: var(--small-font-size);
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
}
.nav__toggle{
    position: fixed;
    top: 1.25rem;
    left: 1.875rem;
    cursor: pointer;
    height: 40px;
    width: 40px;
    background-color: var(--body-color);
    border: 1px solid #e8dfee;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transition: .3s;
}
@media screen and (max-width:1024px){
    .aside{
      left: -110px;
    }
    .nav__toggle{
        display: flex;
    }
    .nav__toggle-open{
       left: 140px;
    }
    .show-menu{
        left: 0;
    }
}